/*
* 运维管理store
*/
import {observable, action} from 'mobx';
import Config from '@/config';
import {requestPost} from "@/utils/api";
import Schemas from "@/utils/Schemas/maintenance";

class ProjectStore {
	@observable tableData = [];  // 列表数据
	@observable allTotalCount = 0; //全部项目数
	@observable requestRepairTotalCount = 0; //申请报修数
	@observable waitAssignCount = 0; //等待分配数
	@observable repairringTotalCount = 0; //正在维修数
	@observable repairedDoneTotalCount = 0; //维修完成数
	@observable repairStatus = []; //报修状态列表
	@observable managers = []; // 项目经理列表
	@observable totalCount = 0;
	@observable params = {
		text: '',
		managers:[],
		state:[],
		orderType:0,
		pageIndex:1,
		pageSize:10
	}
	@observable params2 = {
		text: '',
		managers:[],
		state:[],
		orderType:0,
		pageIndex:1,
		pageSize:10
	}
	//详情页面store
	@observable repairData = [];
	@observable projectName = '';
	@observable product = {};
	@observable project = {}

	// 项目列表查询
	@action
	getProductList(text) {
        this.params.text = text;
		return requestPost('getProductList', Config.maintenance.getProductList, Schemas.maintenance.getProductList, this.params).then((res) => {
			this.tableData = Object.assign([], res.body);
			this.params.pageIndex = res.pageIndex;
			this.params.pageSize = res.pageSize;
			this.totalCount = res.totalCount;
		});
	}

	// 查询项目不同状态分类统计数目
	@action
	getAllTotalCount() {

		return requestPost('getAllTotalCount', Config.maintenance.getAllTotalCount, Schemas.maintenance.getAllTotalCount, {state:0}).then((res) => {
		   this.allTotalCount = res.productCount;
		   this.requestRepairTotalCount = res.state1Count;
		   this.waitAssignCount = res.state2Count;
		   this.repairringTotalCount = res.state3Count;
		   this.repairedDoneTotalCount = res.state4Count;
		});
	}

	// 查询报修状态列表
	@action
	getMaintenanceList(state) {
        this.params2.state = [parseInt(state)];
		return requestPost('getMaintenanceList', Config.maintenance.getMaintenanceList, Schemas.maintenance.getMaintenanceList, this.params2).then((res) => {
			this.tableData = Object.assign([], res.body);
			this.params2.pageIndex = res.pageIndex;
			this.params2.pageSize = res.pageSize;
			this.totalCount = res.totalCount;
		});
	}

	// 查询项目经理
	@action
	getManagers() {
		return requestPost('getManagers', Config.maintenance.getManagers, Schemas.maintenance.getManagers).then((res) => {
			this.managers = Object.assign([], res.map(item=>{
				return {text:item.name, value:item.id}
			}));
		});
	}

	// 根据产品编码查询详细详情
	@action
	getProductMaintenanceInfo(code) {
        // let  tmpData ={
		// 	"project": {"name": "tigertest"},
		// 	"proudct": {"name": "tigertest"},
		// 	"repairData":[{"applicantUser":"张三"},{"applicantUser":"李四"}]
		// };
		// this.projectName = tmpData.project.name;
		// this.repairData = tmpData.repairData;
		// return;
		let params = {"code": code}
		return requestPost('getProductMaintenanceInfo', Config.maintenance.getProductMaintenanceInfo, Schemas.maintenance.getProductMaintenanceInfo, params)
	}

	// 新增或更新报修和确认信息维修信息
	@action
	addOrUpdateRepairAcceptRecord(repairDataItem) {
		let params ={"repairData":repairDataItem}
		return requestPost('addOrUpdateRepairAcceptRecord', Config.maintenance.addOrUpdateRepairAcceptRecord, Schemas.maintenance.addOrUpdateRepairAcceptRecord, params);
	}

	// 更新产品（基础）信息
	@action
	updateProduct(product) {
		let params ={"product":product}
		return requestPost('updateProduct', Config.maintenance.updateProduct, Schemas.maintenance.updateProduct, params);
	}

	// 更新项目信息
	@action
	updateProject(project) {
		let params ={"project":project}
		return requestPost('updateProject', Config.maintenance.updateProject, Schemas.maintenance.updateProject, params);
	}
	@observable supplierList = [];

	//项目供应商
	@action
	getSupplierListByprojectId(project) {
		let params ={"projectId":project}
		return requestPost('getSupplierListByprojectId', Config.maintenance.getSupplierListByprojectId, Schemas.maintenance.getSupplierListByprojectId, params)
		.then((res) => {
			this.supplierList = Object.assign([], res);
			return res;
		});
	}
}


export default ProjectStore;



