/*
*运维管理--项目列表
*qiufh@bocspace.cn
*/
import React from 'react';
import {observer} from 'mobx-react';
import {
	Breadcrumb,
	DatePicker,
	Select,
	Row,
	Col,
	Button,
	Table,
	Pagination,
	Form,
	Typography,
	Tabs,
	Icon,
	Input,
	ConfigProvider,
	Popconfirm,
	message
} from 'antd';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom"
import ProjectStore from '@/store/project/ProjectStore';
import AsyncSelect from '@/components/common/AsyncSelect';
import Config from '@/config';
import Schemas from "@/utils/Schemas/maintenance";
import url from "url";

const {TabPane} = Tabs;
const { Search } = Input;
const {Option} = Select;
const {MonthPicker, RangePicker} = DatePicker;
const {Title} = Typography;

const customizeRenderEmpty = () => (
	<div style={{ textAlign: 'center',padding: '50px 0'}}>
		<img
			width='150px'
			height="100px"
			src='./images/not_found.png' />
		<p style={{
			fontSize:'14px',
			fontFamily:'PingFangSC-Regular,PingFang S',
			fontWeight: '400',
			color: 'rgba(75,80,81,1)'
		}}>暂无数据</p>
	</div>
);

// 暂时前端控制6张表
const statusInfo=['正常', '报修-待确认',  '报修-待分配', '报修-待维修','维修完成'];
// 暂时前端控制6张表
const statusInfo2=['正常', '待确认',  '待分配', '待维修','维修完成'];

@Form.create()
@withRouter
@observer
class ProjectList extends React.Component {
	constructor(props) {
		super(props)
		this.store = new ProjectStore();
	}

	state = {
		visible: false,
		editable: false,
		showRunStatus: false,
		addType: 0,
		reportTypeList: [],
		reportNameList: [],
		userInfo: [],
		name:"", //名称
		type:"", //类型
		keyType: 'type', // 所属类别
		parendId: '',
		activeKey: '0', // 激活的key,
		sortedInfo:null,
		searchInputValue: '',
	}

	componentDidMount() {
		let search = this.props.location.search;
		const activeKey = url.parse(search, true).query.activeKey;
		if(activeKey!=null&&activeKey!=undefined){  // 跳转申请报修
			this.setState({activeKey,searchInputValue:''},()=>{
				this.store.getManagers();
				this.store.getAllTotalCount();
				this.show4StatusList(activeKey);
			})
		} else {
			this.loadPageInfo();
		}
	}

    // 查看
	handleView = (param) => {
		this.props.history.push('/manual?title=view&classifyName='+param.classifyName+'&theme='+param.theme+'&month='+param.month);
	}

	// 编辑
	handleEdit = (param) => {
		this.props.history.push('/manual?title=edit&classifyName='+param.classifyName+'&theme='+param.theme+'&month='+param.month);
	}

	onShowSizeChange = (current, pageSize) => {
		const {activeKey, searchInputValue} = this.state;
		if(activeKey!=="0"){
			this.store.params2 = Object.assign(mobx.toJS(this.store.params2), {
				pageIndex: current,
				pageSize: pageSize
			});
			this.store.getMaintenanceList(activeKey);
		}else{
			this.store.params = Object.assign(mobx.toJS(this.store.params), {
				pageIndex: current,
				pageSize: pageSize
			});
			this.store.getProductList(searchInputValue);
		}
	}

	onChange = (pageNumber) => {
		const {activeKey, searchInputValue} = this.state;

		if(activeKey!=="0"){
			this.store.params2 = Object.assign(mobx.toJS(this.store.params2), {
				pageIndex: pageNumber
			});
			this.store.getMaintenanceList(activeKey);
		}else{
			this.store.params = Object.assign(mobx.toJS(this.store.params), {
				pageIndex: pageNumber
			});
			this.store.getProductList(searchInputValue);
		}

	}

	loadPageInfo = () =>{
		this.store.getManagers();
		this.store.getAllTotalCount();
		this.store.getProductList('');
	}

	clearPageStatus=()=>{
		this.store.params =  Object.assign(mobx.toJS(this.store.params), {
			text: '',
			managers:[],
			state:[],
			pageIndex:1,
			pageSize:10
		});
		this.store.params2 = Object.assign(mobx.toJS(this.store.params2), {
			text: '',
			managers:[],
			pageIndex:1,
			pageSize:10
		});
	}

	tabsOnchange=(key)=>{
		this.setState({activeKey:key, searchInputValue:''});
        this.clearPageStatus();
        switch(key){
			case '1':
				this.show4StatusList(key);
				break;
			case '2':
				this.show4StatusList(key);
				break;
			case '3':
				this.show4StatusList(key);
				break;
			case '4':
				this.show4StatusList(key);
				break;
			default:
				this.setState({showRunStatus:false})
				this.loadPageInfo();
		}
	}

	show4StatusList=(key)=>{
		this.setState({showRunStatus:true})
		this.store.getMaintenanceList(key);
	}

	setRepairRecordSort = () => {
        this.setState({
			sortedInfo:{
				order: 'descend',
				columnKey: 'month'
			}
		})
	}

	showRunStatus =(text, record)=>{
		const { showRunStatus } = this.state;
		let color = {"color":"#ff8c00","cursor":"pointer"};
		if(text===0 || text===4) color = {"color":"#00aa00","cursor":"pointer"};
		if(showRunStatus) color = {"color":"#0991FF","cursor":"pointer"};
		return (<span  onClick={()=>{this.openDetailsByRepairState(text, record)}} style={color}>{showRunStatus? statusInfo2[text] : statusInfo[text]}</span>)
	}

	filterRunStatus = (value, record)=>{
		return value===statusInfo[record.state]
	}

	onSearch = (value, event) => {
		const {activeKey} = this.state;
		if(activeKey!="0"){
			this.store.params2.text = value;
            this.store.getMaintenanceList(activeKey);
		}else{
			this.store.getProductList(value);
		}

	}

	handleGetSearchInputValue = (event) => {
		this.setState({
			searchInputValue : event.target.value,
		})
	};

	openDetails = (code) => {
		let _this = this;
		_this.props.history.push('/project/details?code='+code);
	}

	openDetailsByAllProjectRecord = (text, record) => {
		let _this = this;
		_this.props.history.push('/project/details/maintenanceinfo?code='+record['product']['code']+'&allprostate='+record.state);
	}

	openDetailsByRepairStateRecord = (text, record) => {
		let _this = this;
		_this.props.history.push('/project/details?code='+record['product']['code']+'&statuslistid='+record.id);
	}

	openDetailsByRepairState = (text,record) => {
		const { activeKey } = this.state;
		if(activeKey!=="0"){
			this.openDetailsByRepairStateRecord(text,record);
		}else {
			this.openDetailsByAllProjectRecord(text,record);
		}
	}

	getDataByTableChange = (order) =>{
	    const {activeKey, searchInputValue} = this.state;
		const {columnKey} = order;
       if(order.order==null) {
		  this.store.params.orderType = 0;
		  this.store.params2.orderType = 0;
		  activeKey==='0'? this.store.getProductList(searchInputValue) :  this.store.getMaintenanceList(activeKey);
		  return;
	   }
       if (columnKey=="recordCount") { // 维修记录
	       if(order.order=="descend"){
		       this.store.params.orderType = 1;
		       this.store.params2.orderType = 1;
	       }
	       if(order.order=="ascend"){
		       this.store.params.orderType = 2;
		       this.store.params2.orderType = 2;
	       }
       } else if (columnKey=="createTime") {
	       if(order.order=="descend"){
		       this.store.params.orderType = 3;
		       this.store.params2.orderType = 3;
	       }
	       if(order.order=="ascend"){
		       this.store.params.orderType = 4;
		       this.store.params2.orderType = 4;
	       }
       } else if (columnKey=="repairTime") {
	       if(order.order=="descend"){
		       this.store.params.orderType = 5;
		       this.store.params2.orderType = 5;
	       }
	       if(order.order=="ascend"){
		       this.store.params.orderType = 6;
		       this.store.params2.orderType = 6;
	       }
       } else if (columnKey=="confirmationTime") {
	       if(order.order=="descend"){
		       this.store.params.orderType = 7;
		       this.store.params2.orderType = 7;
	       }
	       if(order.order=="ascend"){
		       this.store.params.orderType = 8;
		       this.store.params2.orderType = 8;
	       }
       }
	   activeKey==='0'? this.store.getProductList(searchInputValue) :  this.store.getMaintenanceList(activeKey);
	}

	render() {
		const {editable, activeKey, showRunStatus, searchInputValue} = this.state;
		const listStyle = {
			display: editable ? 'none' : 'block'
		};

		const extraContent = (
			<Search className={'extraContentSearch'} placeholder="请输入" onChange={this.handleGetSearchInputValue} value={ searchInputValue } onSearch={this.onSearch} />
		);

		let columns = [
			{
				title: 'ID编码', dataIndex: 'product.code', align: 'left',
			    render:(text, record)=>{
					return(<a  onClick={(e)=>{ this.openDetails(text)}} >{text}</a>)
			 	}
			},
			{title: '项目名称', dataIndex: 'project.name', align: 'center'},
			{title: '生产厂商', dataIndex: 'product.producer', align: 'center',className: activeKey=="4"?"hidden":""},
			{title: '维修单位', dataIndex: 'repairUnit', align: 'center',className: activeKey=="4"?"":"hidden"},
			{
				title: '项目经理', dataIndex: 'project.managers', align: 'center',
				filters: mobx.toJS(this.store.managers),
				filterIcon: filtered => (
					<Icon className="bocfiterIcon" type="caret-down" style={{ color: filtered ? '#1890ff' : undefined }} />
				),
				onFilter: (value, record) => {
					return record.project.managers === value
				},
			},
			{
				title: '启用时间', dataIndex: 'project.usingTime', align: 'center', className: activeKey=="4"?"hidden":"", render: (text, record) => {
					return text===0 ? "": moment(text).format("YYYY.MM.DD")
				}
			},
			{
				title: '报修时间', dataIndex: 'createTime', align: 'center', className: activeKey!="0"?"":"hidden",
				sorter:true,
				render: (text, record) => {
					return text===0 ? "": moment(text).format("YYYY.MM.DD")
				}
			},
			{
				title: '受理时间', dataIndex: 'repairTime', align: 'center', className: activeKey=="2"||activeKey=="3"||activeKey=="4"?"":"hidden",
				sorter:true,
				render: (text, record) => {
					return text===0 ? "": moment(text).format("YYYY.MM.DD")
				}
			},
			{
				title: '维修完成时间', dataIndex: 'confirmationTime', align: 'center', className: activeKey=="4"?"":"hidden",
				sorter:true,
				render: (text, record) => {
					return text===0 ? "": moment(text).format("YYYY.MM.DD")
				}
			},
			{title: '维修记录', dataIndex: 'recordCount', align: 'center', key:'recordCount', className: activeKey!="0"?"hidden":"",
			 sorter:true,
			 render:(text, record) => {
				 if(activeKey!=0){
				  return	(<span  >{text+'次'}</span>)
				 }else{
				 return (<a onClick={(e)=>{this.openDetailsByRepairState(text, record)}}>{text+'次'}</a>)

				 }
			 }
			},
            !showRunStatus ? {
				title: '运行状态', dataIndex: 'state', align: 'center', key:'state',
				filters: [
					{
					text: '正常',
					value: '正常',
					},
					{
					text: '报修-待确认',
					value: '报修-待确认',
					},
					{
					text: '报修-待分配',
					value: '报修-待分配',
					},
					{
					text: '报修-待维修',
					value: '报修-待维修',
					},
				],
				filterIcon: filtered => (
					<Icon className="bocfiterIcon" type="caret-down" style={{ color: filtered ? '#1890ff' : undefined }} />
				),
				onFilter: (value, record) => {
					return this.filterRunStatus(value, record)
				},
				render:(text, record) =>{ return this.showRunStatus(text, record)}
			}:{title: '状态',
			   dataIndex: 'state',
			   align: 'center',
			   key:'state',
			   render:(text, record) =>{ return this.showRunStatus(text, record)}
			}
		];


		return (
			<div style={{width: '100%', padding:'10px 15px'}}>
				<div style={listStyle}>
					<Tabs tabBarExtraContent={extraContent}  activeKey={activeKey} onChange={this.tabsOnchange}>
					<TabPane
						tab={<span>全部项目 ({this.store.allTotalCount})</span>}
						key="0">

					</TabPane>
					<TabPane
						tab={<span>申请报修 ({this.store.requestRepairTotalCount})</span>}
						key="1">

					</TabPane>
					<TabPane
						tab={<span>等待分配 ({this.store.waitAssignCount})</span>}
						key="2">
					</TabPane>
					<TabPane
						tab={<span>正在维修 ({this.store.repairringTotalCount})</span>}
						key="3">
					</TabPane>
					<TabPane
						tab={<span>维修完成 ({this.store.repairedDoneTotalCount})</span>}
						key="4">
					</TabPane>
					</Tabs>
					<Row type="flex" justify="space-between" className="mt20">
						<Col span={24}>
							<Title className="dib f15"
	level={4}>{ activeKey==0 ? "项目列表" : activeKey==1 || activeKey==2 ? "报修列表" :"维修列表" }</Title>
						</Col>
					</Row>
					<Row type="flex" justify="space-between" style={{"marginTop": "0px"}}>
						<Col span={24}>
								<ConfigProvider renderEmpty={customizeRenderEmpty}>
								<Table
								    className="m_table"
									columns={columns}
									dataSource={mobx.toJS(this.store.tableData)}
									size="middle"
									rowKey="id"
									pagination={false}
									onChange={(pagination, filters, sorter)=>{this.getDataByTableChange(sorter)}}
								/>
								<Pagination
									className="mt20 tr"
									showSizeChanger
									onShowSizeChange={this.onShowSizeChange}
									onChange={this.onChange}
									total={this.store.totalCount}
									showQuickJumper
									current={ activeKey==="0" ? mobx.toJS(this.store.params.pageIndex) : mobx.toJS(this.store.params2.pageIndex) }
									showTotal={total => `共 ${total} 条记录`}
								/>
								</ConfigProvider>

						</Col>
					</Row>
				</div>
			</div>
		)
	}
}


export default ProjectList



