/*
* 基于antd select组件封装 单选
* dangw@glodon.com
* */
import React from 'react';
import {Select} from 'antd';
import PropTypes from 'prop-types';
const Option = Select.Option;

export default class AsyncSelect extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      value: '',
      keyName:''
    };
  }

  componentWillReceiveProps(nextProps) {
    if ('value' in nextProps && this.props.value !== nextProps.value) {
      this.setState({
        value: nextProps.value||"",
        keyName: nextProps.keyName,
      });
    }
  }

  componentDidMount() {
    if (!this.props.url) {
      return;
    }
    this.handleFocus();
  }

  handleChange = (val, e) => {
    //if (value === undefined) return false;
    const value = e.props.val;
    this.setState({value: value[this.props.keyName]});
    let {onChange} = this.props;
    if (onChange) {
      onChange(val, e);
    }
  }

  handleFocus = () => {
    fetch(this.props.url, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
      },
      body: JSON.stringify({
        query: this.props.query,
        variables: this.props.variables
      })
    })
    .then(response => response.json())
    .then(json => {
      if (json.data!==null&&json.data[this.props.type]!==null) {
        let array;
        // 报表类别过滤数据
        if (this.props.keyType == "type") {
          array = json.data[this.props.type].filter((item)=>{return item.id!=="0"});
        } else {
          array = json.data[this.props.type];
        }
        this.setState({
          data:array
        });
      }
    });
  }

  render() {
    const options = this.state.data.length&&this.state.data.map((d,i)=> <Option value={d[this.props.keyName]} key={i} val={d}>{d.name}</Option>);

    return (
      <Select
        value={this.props.value}
        placeholder={this.props.placeholder||"请选择"}
        style={this.props.style}
        onChange={this.handleChange}
        onFocus={this.handleFocus}
        onBlur={this.props.onBlur}
        disabled={this.props.disabled}
      >
        {options}
      </Select>
    );
  }
}

AsyncSelect.propTypes = {
  style: PropTypes.object,
  placeholder: PropTypes.string,
  value: PropTypes.string, // 默认值
  url: PropTypes.string,
  dataMapFunc: PropTypes.func,
  onChange: PropTypes.func,
};



